<template>
    <div class="home">
        <Topnav></Topnav>
        <div class="topbanner">
            <el-image
                style="width: 100%; height: 500px"
                :src="require('../assets/images/bgbackphoto.jpg')"
                fit="cover">
            </el-image>
            <p>Technology Blog</p>
        </div>
        <div class="article-con">
            <el-row :gutter="20">
                <el-col :span="6">
                    <div class="grid-content">
                        <div class="shadown">
                            <p class="f-mb20 tit">文章分类</p>
                            <div>
                                <el-tag size="medium">nodejs</el-tag>
                                <el-tag type="success" size="medium">mysql</el-tag>
                                <el-tag type="success" size="medium">javascript</el-tag>
                                <el-tag type="success" size="medium">WEB</el-tag>
                                <el-tag type="info" size="medium">标签三2</el-tag>
                                <el-tag type="info" size="medium">标签12三</el-tag>
                                <el-tag type="warning" size="medium">标签四</el-tag>
                                <el-tag type="warning" size="medium">标签四</el-tag>
                                <el-tag type="danger" size="medium">标签五</el-tag>
                            </div>
                        </div>
                    </div>
                </el-col>
                <el-col :span="18">
                    <div class="grid-content">
                        <div class="shadown">
                            <p class="f-mb20 tit">文章列表</p>
                            <el-input
                                placeholder="请输入搜索标题"
                                v-model="search"
                                clearable
                                class="search-inp">
                            </el-input>
                            <br>
                            <div style="margin: 10px 80px 0 0; text-align: right;">
                                <el-dropdown @command="handleCommand">
                                    <span class="el-dropdown-link">
                                        {{xlcd}}<i class="el-icon-arrow-down el-icon--right"></i>
                                    </span>
                                    <el-dropdown-menu slot="dropdown">
                                        <el-dropdown-item command="最新更新">最新更新</el-dropdown-item>
                                        <el-dropdown-item command="倒序" divided>倒序</el-dropdown-item>
                                    </el-dropdown-menu>
                                </el-dropdown>
                            </div>
                            <el-row>
                                <el-col :span="24" class="f-mt20" style="padding: 10px 80px; cursor: pointer;">
                                    <el-card shadow="hover">
                                        <el-image src="https://img0.baidu.com/it/u=3694508586,2010976765&fm=253&fmt=auto&app=138&f=JPEG?w=650&h=366" style="width: 100%; height: 200px" fit="cover"></el-image>
                                        <h2>nodejs express 搭建服务器</h2>
                                    </el-card>
                                </el-col>
                                <el-col :span="24" class="f-mt20" style="padding: 10px 80px; cursor: pointer;">
                                    <el-card shadow="hover">
                                        <el-image src="https://img2.baidu.com/it/u=2075053484,2658765595&fm=253&fmt=auto&app=138&f=JPEG" style="width: 100%; height: 200px" fit="cover"></el-image>
                                        <h2>mysql安装，连接及工具包的选择</h2>
                                    </el-card>
                                </el-col>
                            </el-row>
                        </div>
                    </div>
                </el-col>
            </el-row>
        </div>
        <!-- <HelloWorld :login='login' @User='getUser'></HelloWorld>
        helloword 子组件传递来的值：{{msg}}
        <hr> -->
        <!-- 我是批量注册的基础组件<Test :parentFunName="parentFun"></Test>
        <hr>
        <button @click="goList">list</button> -->
        <editor></editor>
        <edition v-model="article.content"></edition>
<el-skeleton /> 11221
    </div>  
</template>
<script>
import  editor  from "../components/editor/index.vue";
import  edition  from "../components/editor/edition.vue";
export default {
    name:'home',
    components: { 
        editor ,edition
    },
    data(){
        return {
            login : 'loginName',
            msg:'',
            load:{data:{}},
            radio:"0",
            bgurl:"../assets/images/bgbackphoto.jpg",
            search: "",//搜索
            xlcd: "最新更新",//搜索排序
            article:{
                content:"正文"
            }
        }
    },
    methods:{
        getUser: function(msg){ //父组件获取子组件值
            this.msg = msg
        },
        // 获取数据
        loadData(numId){
            let sendData = {}
            numId && (sendData.id = numId)
            console.log(sendData);
            this.$api.article.getUserList(sendData).then(res=>{
                console.log(res);
                this.load=res
            }).catch(err=>{
                console.log(err);
            })
        },
        // 获取数据
        addUser(numId){
            let sendData = {}
            numId && (sendData.id = numId)
            console.log(sendData);
            this.$api.article.addUser(sendData).then(res=>{
                console.log(res);
            }).catch(err=>{
                console.log(err);
            })
        },
        // 去list页面
        goList(){
            this.$router.push({path:'/list/'})
        },
        parentFun(){
            console.log("我是父组件的方法")
        },
        handleCommand(command) {
            this.xlcd = command
        }
        
    },
    mounted(){
        this.loadData(6);
        this.loadData();
        this.addUser(1)
        
    }
}
</script>
<style lang="less" scoped>
.topbanner{
    margin-top: -1px;
    position: relative;
    &::after{
        content: '';
        position: absolute;
        top: 0;
        right: 0;
        bottom: 4px;
        left: 0;
        background: rgba(0, 0, 0, .5);
    }
    p{
        color: #fff;
        position: absolute;
        width: 100%;
        top: 50%;
        margin-top: -18px;
        z-index: 1;
        font-size: 28px;
        text-align: center;
    }
}
.article-con{
    max-width: 1200px;
    margin: 0 auto;
    .grid-content{
        margin-top: 20px;
        .shadown{
            padding: 20px 10px;
            box-shadow: 0 0 5px 1px rgba(0, 0, 0, .1);
            .tit{
                font-size: 18px;
                font-weight: bold;
            }
            .search-inp{
                width: 50%;
                height: 50px;
                line-height: 50px;
                margin: 0 auto; 
                /deep/ input {
                    height: 50px;
                    border: 1px solid rgba(76, 89, 133, .5);
                }
            }
            /deep/ .el-tag{
                padding: 0 10px;
                height: 40px;
                line-height: 40px;
                margin: 10px;
            }
        }
    }
}


</style>
<style lang="less">
</style>
                


