<template>
  <div>
      list 
        <button @click="goDetail">goDetail</button>

  </div>
</template>
<script>
export default {
  name: "list",
  data() {
    return {};
  },
  mounted() {
    alert("加载list");
  },
  methods: {
    // 去detail页面
    goDetail() {
      this.$router.push({ path: "/list/detail/"});
    },
  },
};
</script>


