// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store' // 导入vuex文件
import api from './api' // 导入api接口

import './assets/css/comm.scss' //导入自定义公共样式

import { Button, Select, Radio, Image, Menu, Submenu, MenuItem, MenuItemGroup, Row, Col, Input, InputNumber, Avatar, Tag, Card, Dropdown, DropdownMenu, DropdownItem, Upload  } from 'element-ui'; //按需引入element ui
// import 'element-ui/lib/theme-chalk/index.css';
import './element-variables.scss'
Vue.prototype.$ELEMENT = { size: 'small', zIndex: 3000 }; //按需引入调整全局配置
Vue.use(Button);
Vue.use(Select);
Vue.use(Radio);
Vue.use(Image);
Vue.use(Menu);
Vue.use(Submenu);
Vue.use(MenuItem);
Vue.use(MenuItemGroup);
Vue.use(Row);
Vue.use(Col);
Vue.use(Input);
Vue.use(InputNumber);
Vue.use(Avatar);
Vue.use(Tag);
Vue.use(Card);
Vue.use(Dropdown);
Vue.use(DropdownMenu);
Vue.use(DropdownItem);
Vue.use(Upload);

Vue.config.productionTip = false

Vue.prototype.$api = api // 将api挂载到vue的原型上


// 全局注册基础组件
import comm from './common.js'
comm.zdqj();

// 全局路由守卫
router.beforeEach((to, from, next) => {
  // 判断前往的页面是否需要登录或者权限
  if (to.meta.requireAuth) {
    // 判断权限是否具有
    if (store.state.hasLogin) {
      next()
    } else {
      next({ path: '/login' })
    }
  } else {
    next()
  }

  // 前进刷新后退不刷新
  let toDepth = to.path.split('/').length
  let fromDepth = from.path.split('/').length
  console.log(toDepth,fromDepth);
  if (toDepth < fromDepth) {
    // console.log('back...')
    from.meta.keepAlive = false
    to.meta.keepAlive = true
  }
})

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



