<template>
  <div class="hello">
    my login name is {{login}}
    <hr>
    <!-- 子组件向父组件传值 -->
    <input type="text" @change="setUser" v-model="userPsd">
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      userPsd: ''
    }
  },
  props:['login'], //父组件传值
  methods:{
    setUser:function(){
      this.$emit("User",this.userPsd) //子组件传值给父组件
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



