import Vue from 'vue'
import Router from 'vue-router'
import refresh from '../view/refresh.vue' //断网中转页面
import home from '../view/home.vue'
import list from '../view/list.vue'
import detail from '../view/detail.vue'

Vue.use(Router)

export default new Router({
  mode:'hash',
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/list/',
      name: 'list',
      component: list,
      meta: {
        requireAuth: true, //是否有权限进入，true需要登录
        keepAlive: true, //是否缓存住该页面
        title: '商品列表'
      }
    },
    {
      path: '/list/detail/',
      name: 'detail',
      component: detail,
      meta: {
        keepAlive: true,
        title: '商品内容'
      }
    },
    {
      path: '/refresh',
      name: 'refresh',
      component: refresh
    }
  ]
})



