/**
 * article模块接口列表
 */
import config from './config'
import axios from '@/utils/http'
import qs from 'qs'

const article = {
    //   
    getUserList(params) {
        return axios.get(`${config.apiUrl}/user/getUserList`,{ params });
    },
    //   
    addUser(params) {
        return axios.post(`${config.apiUrl}/user/addUser`, qs.stringify(params));
    },
    // 新闻详情
    articleDetail(id, params) {
        return axios.get(`${config.apiUrl}/topics/${id}`, { params });
    },
    // post提交
    login(params) {

        return axios.post(`${config.apiUrl}/login`, qs.stringify(params));
    }
}

export default article;



